body {
  font-family: 'Roboto Condensed', sans-serif;
}
.App {
  text-align: center;
  background-color: #b9fde0;
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.desc-container {
  background-color: skyblue;
  width: 40%;
}

.parent-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.intro-container {
  margin-left: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* position: absolute; */
  width: 40%;
  z-index: 2;
}

.inner-intro {
  padding: 10px;
  background-color: white;
  border-radius: 2px;
  padding-bottom: 20px;

}

.title {
  font-size: 64px;
  font-weight: 300;
  letter-spacing: 3px;
}

.subtitle {
  font-size: 32px;
  font-weight: 300;
}

