.airpod {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
  position: absolute;
  width: 6%;
  height: 20%;
  top: 540px;
  left: 230px;
  cursor: pointer;
}

.boy-image {
  height: 900px;
  width: 800px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
